import PropTypes from 'prop-types';

import Head from "next/head";
import Router from "next/router";

import { Form, Input, Checkbox, Button } from "antd";

import Layout from "../components/Layout";
import { signUpAction, SIGN_UP_REQUEST } from "../reducers/user";

const TextInput = ({ value }) => (
  <div>{value}</div>
);

TextInput.propTypes = {
  value: PropTypes.string,
};


//  custom hook
export const useInput = (initValue = null) => {
  const [value, setter] = useState(initValue);
  const handler = useCallback(e => {
    setter(e.target.value);
  }, []);
  return [value, handler];
};

const Signup = () => {
  //   const [id, setId] = useState("");
  //   const [nick, setNick] = useState("");
  //   const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [passwordError, setPasswordError] = useState(false);
  const [term, setTerm] = useState(false);
  const [termError, setTermError] = useState(false);

  const [id, onChangeId] = useInput("");
  const [nick, onChangeNick] = useInput("");
  const [password, onChangePassword] = useInput("");

  const dispatch = useDispatch();
  const { isSigningUp, me } = useSelector(state => state.user);

  useEffect(() => {
    if (me) {
      alert(`Login succeeded, we're going to the main page`)
      Router.push("/");
    }
  }, [me && me.id]);

  const onSubmit = useCallback(
    e => {
      e.preventDefault();
      if (password !== passwordConfirm) {
        return setPasswordError(true);
      }
      if (!term) {
        return setTermError(true);
      }
      // dispatch(signUpAction({
      //   id,
      //   password,
      //   nick
      // }))
      return dispatch({
        type: SIGN_UP_REQUEST,
        data: {
          userId: id,
          password,
          nickname:nick
        }
      });

      console.log({
        id,
        nick,
        password,
        passwordConfirm,
        term
      });
      // useCallbackÏùÑ Ïì∏ Îïê Ìï®Ïàò ÎÇ¥Î∂ÄÏóêÏÑú Ïì∞Îäî stateÎ•º dependency Î∞∞Ïó¥Ïóê ÎÑ£Ïñ¥Ï§ÄÎã§
      // [password, passwordConfirm, term]
    },
    [id, nick, password, passwordConfirm, term]
  );

  /*
  const onChangeId = e => {
    setId(e.target.value);
  };

  const onChangeNick = e => {
    setNick(e.target.value);
  };

  const onChangePassword = e => {
    setPassword(e.target.value);
  };
  */

  const onChangePasswordConfirm = useCallback(
    e => {
      setPasswordError(e.target.value !== password);
      setPasswordConfirm(e.target.value);
    },
    [password]
  );

  const onChangeTerm = useCallback(e => {
    setTermError(false);
    setTerm(e.target.checked);
  }, []);

  return (
    <div>
      <Form onSubmit={onSubmit} style={{ padding: 10 }}>
      <TextInput value="135135" />

        <div>
          <label htmlFor="user-id">ID</label>
          <Input
            type="text"
            name="user-id"
            value={id}
            required
            onChange={onChangeId}
          />
        </div>
        <div>
          <label htmlFor="user-nick">Nick Name</label>
          <Input
            name="user-nick"
            value={nick}
            required
            onChange={onChangeNick}
          />
        </div>
        <div>
          <label htmlFor="user-password">Password</label>
          <Input
            name="user-password"
            type="password"
            required
            onChange={onChangePassword}
            value={password}
          />
        </div>
        <div>
          <label htmlFor="user-password-confirm">Password Confirm</label>
          <Input
            name="user-password-confirm"
            type="password"
            required
            onChange={onChangePasswordConfirm}
            value={passwordConfirm}
          />
          {passwordError && (
            <div style={{ color: "red" }}>Password should match</div>
          )}
        </div>
        <div>
          <Checkbox name="user-term" onChange={onChangeTerm} value={term}>
            I agree with the term
          </Checkbox>
          {termError && (
            <div style={{ color: "red" }}>
              Please check the box 'I agree with the term'
            </div>
          )}
        </div>
        <div style={{marginTop: 10}}>
          <Button type="primary" htmlType="submit" loading={isSigningUp}>
            Done
          </Button>
        </div>
      </Form>
    </div>
  );
};

export default Signup;
