const reducer = (state = initState, action) => {
  switch (action.type) {
    case LOG_IN_REQUEST:
      // console.log(action.data)
      return {
        ...state,
        // isLoggedIn: true,
        // user: action.data
        // user: dummyUser
        isLoggingIn: true,
        loginError: ""
      };

    case LOG_IN_SUCCESS: {
      return {
        ...state,
        isLoggingIn: false,
        // isLoggedIn: true,
        me: action.data
      };
    }
    case LOG_IN_FAILURE: {
      return {
        ...state,
        isLoggingIn: false,
        // isLoggedIn: false,
        loginError: action.error,
        me: null
      };
    }
    case LOG_OUT_REQUEST: {
      return {
        ...state,
        isLoggingOut: true
      };
    }
    case LOG_OUT_SUCCESS:
      return {
        ...state,
        isLoggingOut: false,
        // isLoggedIn: false,
        me: null
      };
    case SIGN_UP_REQUEST:
      return {
        ...state,
        isSigningUp: true,
        isSignedUp: false,
        signUpError: ""
      };
    case SIGN_UP_SUCCESS:
      return {
        ...state,
        isSigningUp: false,
        isSignedUp: true
      };
    case SIGN_UP_FAILURE:
      return {
        ...state,
        isSigningUp: false,
        signUpError: action.error
      };
    case LOAD_USER_REQUEST:
      return {
        ...state
      };
    case LOAD_USER_SUCCESS:
      return {
        ...state,
        me: action.data
      };
    case LOAD_USER_FAILURE:
      return {
        ...state
      };

    default:
      return state;
  }
};

export default reducer;
